import { toRefs, reactive } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'tongzhiheader',
  setup() {
    const router = useRouter();
    const data = reactive({
      tongzhi: [{
        id: 'T001',
        title: '所有通知'
      }, {
        id: 'T002',
        title: '道具通知'
      }, {
        id: 'T003',
        title: '活动通知'
      }, {
        id: 'T004',
        title: '财务通知'
      }, {
        id: 'T005',
        title: '系统通知'
      }],
      title: '所有通知',
      num: 0,
      showarrow: true,
      showothers: false
    });
    const toup = () => {
      router.go(-1);
    };
    const tongzhiclick = i => {
      data.num = i;
      data.title = data.tongzhi[i].title;
      data.showothers = false;
      data.showarrow = !data.showarrow;
    };
    const changetongzhi = () => {
      data.showarrow = !data.showarrow;
      data.showothers = !data.showothers;
    };
    return {
      ...toRefs(data),
      toup,
      tongzhiclick,
      changetongzhi
    };
  }
};